.sidenav-container {
    height: 100vh;
}

.drawer {
    width: 90%;
    max-width: none;
    transition: transform 0.6s ease-in-out, width 0.6s ease-in-out;
}

.mat-sidenav {
    width: 90%;
    max-width: none;
    border: none;
    transition: margin-left 0.6s;
}

.mat-sidenav-content {
    transition: margin-left 0.6s ease-in-out;
}

.drawer-open {
    margin-left: 200px !important;
}

.mat-toolbar{
    transition: background-color 0.6s ease-in-out;
}

.example-accordion {
    display: block;
    max-width: 500px;
  }
  
  .example-accordion-item {
    display: block;
    border: solid 1px #ccc;
  }
  
  .example-accordion-item + .example-accordion-item {
    border-top: none;
  }
  
  .example-accordion-item-header {
    display: flex;
    align-content: center;
    justify-content: space-between;
  }
  
  .example-accordion-item-description {
    font-size: 0.85em;
    color: #999;
  }
  
  .example-accordion-item-header,
  .example-accordion-item-body {
    padding: 16px;
  }
  
  .example-accordion-item-header:hover {
    cursor: pointer;
    background-color: #eee;
  }
  
  .example-accordion-item:first-child {
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }
  
  .example-accordion-item:last-child {
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
  }
  

